<h2>属性</h2>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>mapStyle</td>
<td>String</td>
<td><code v-pre>'img_w'</code></td>
<td><code v-pre>optional</code> 天地图服务地图类型。</td>
</tr>
<tr>
<td>credit</td>
<td>String|Object</td>
<td><code v-pre>'天地图全球影像服务'</code></td>
<td><code v-pre>optional</code> 服务版权描述信息。</td>
</tr>
<tr>
<td>token</td>
<td>String</td>
<td></td>
<td><code v-pre>optional</code> 天地图应用 key。 <a href="http://lbs.tianditu.gov.cn/home.html">申请地址</a></td>
</tr>
<tr>
<td>protocol</td>
<td>String</td>
<td><code v-pre>https</code></td>
<td><code v-pre>optional</code> 指定请求协议类型。可以是<code v-pre>https</code>或者<code v-pre>http</code></td>
</tr>
<tr>
<td>minimumLevel</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 最小层级。</td>
</tr>
<tr>
<td>maximumLevel</td>
<td>Number</td>
<td><code v-pre>20</code></td>
<td><code v-pre>optional</code> 最大层级。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>mapStyle 可取值：
<ul>
<li>'cia_c': 天地图全球中文注记服务（经纬度坐标系）。</li>
<li>'cia_w': 天地图全球中文注记服务（墨卡托投影坐标系）。</li>
<li>'cta_c': 天地图全球地形中文注记服务（经纬度坐标系）。</li>
<li>'cta_w': 天地图全球地形中文注记服务（墨卡托投影坐标系）。</li>
<li>'cva_c': 天地图全球矢量中文注记服务（经纬度坐标系）。</li>
<li>'cva_w': 天地图全球矢量中文注记服务（墨卡托投影坐标系）。</li>
<li>'ela_c': 天地图全球影像英文注记服务（经纬度坐标系）。</li>
<li>'ela_w': 天地图全球影像英文注记服务（墨卡托投影坐标系）。</li>
<li>'eva_c': 天地图全球矢量英文注记服务（经纬度坐标系）。</li>
<li>'eva_w': 天地图全球矢量英文注记服务（墨卡托投影坐标系）。</li>
<li>'img_c': 天地图全球影像地图服务（经纬度坐标系）。</li>
<li>'img_w': 天地图全球影像地图服务（墨卡托投影坐标系）。</li>
<li>'ter_c': 天地图全球地形晕渲服务（经纬度坐标系）。</li>
<li>'ter_w': 天地图全球地形晕渲服务（墨卡托投影坐标系）。</li>
<li>'vec_c': 天地图全球矢量地图服务（经纬度坐标系）。</li>
<li>'vec_w': 天地图全球矢量地图服务（墨卡托投影坐标系）。</li>
</ul>
</li>
</ul>
<ul>
<li>参考超图官方文档： <strong><a href="http://support.supermap.com.cn:8090/webgl/Build/Documentation/TiandituImageryProvider.html">TiandituImageryProvider</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例。</td>
</tr>
<tr>
<td>errorEvent</td>
<td>TileProviderError</td>
<td>当图层的提供者发生异步错误时触发, 返回一个 TileProviderError 实例。</td>
</tr>
<tr>
<td>readyPromise</td>
<td>ImageryProvider</td>
<td>当图层可用时触发, 返回 ImageryProvider 实例。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          protocol: 'http',
          options: [
            {
              value: 'img_c',
              label: '全球影像地图服务(经纬度)'
            },
            {
              value: 'img_w',
              label: '全球影像地图服务(墨卡托)'
            },
            {
              value: 'vec_c',
              label: '全球矢量地图服务(经纬度)'
            },
            {
              value: 'vec_w',
              label: '全球矢量地图服务(墨卡托)'
            },
            {
              value: 'ter_c',
              label: '全球地形晕渲服务(经纬度)'
            },
            {
              value: 'ter_w',
              label: '全球地形晕渲服务(墨卡托)'
            }
          ],
          mapStyle: 'img_c',
          alpha: 1,
          brightness: 1,
          contrast: 1,
          sortOrder1: 20,
          sortOrder2: 10
        }
      },
      methods: {
        ready({ Cesium, viewer }) {
          this.Cesium = Cesium
          this.viewer = viewer
          window.vm = this
          window.viewer = viewer
        }
      }
    }
  </script>